.CartItem{
    margin: 1rem 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.CartItem__img{
    margin-right: 3rem;
    height: 4rem;
}
.CartItem__content{
    display: flex;
}
.CartItem__title{
    font-size: 1rem;
}
.CartItem__remove{
    border:none;
    background: none;
    cursor: pointer;
}
.CartItem__remove img{
    width: 1.5rem;
}
.Cart__empty_cart_msg{
    text-align: center;
    color: #666;
}
@media(max-width: 700px) {
    .CartItem__img{
        margin-right: 1.5rem;
        height: 4rem;
    }
}